import React, {useEffect, useRef, useState} from "react";
import PlayButton from "./PlayButton";
import PauseButton from "./PauseButton";
import LoadingAudioButton from "./LoadingAudioButton";
import VolumeSlider from "./VolumeSlider";
import Grid from "@material-ui/core/Grid";
import {debounce} from 'lodash';


const Player = () => {
    // const classes = useStyles();
    const [isPlaying, setIsPlaying] = useState(false);
    const [canPLay, setCanPlay] = useState(true);
    const [volume, setVolume] = useState(70);

    const player = useRef();


    useEffect(() => {
        player.current.onloadstart = () => console.log("onloadstart");
        player.current.oncanplay = () => console.log("oncanplay");
        player.current.onended = () => console.log("onended");
        player.current.onpause = () => console.log("onpause");
        player.current.onWaiting = () => console.log("onWaiting");
    }, []);


    useEffect(() => {
        player.current.volume = volume / 100;
    }, [volume]);

    const play = () => {
        player.current.play();
        setIsPlaying(true);
    };
    const pause = () => {
        player.current.pause();
        setIsPlaying(false);
    };

    const debounceSetVolume = debounce(setVolume, 100);

    return (
        <>
            <Grid container>
                <Grid item>
                    {
                        canPLay ?
                            isPlaying ? <PauseButton onClick={() => pause()}/> : <PlayButton onClick={() => play()}/>
                            : <LoadingAudioButton/>
                    }
                </Grid>
                <Grid item>
                    <VolumeSlider onChange={(value) => debounceSetVolume(value)}/>
                </Grid>
            </Grid>
            <audio ref={player}>
                <source src="https://de1.internet-radio.com/proxy/radiouibceb?mp=/stream" type="audio/mp3" on/>
            </audio>
        </>
    );
};

export default Player;